.list.title {
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    border-bottom: solid 1px indianred;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

ul.list.content {
    list-style: none;
}

ul.list.content>li {
    border-bottom: solid 1px floralwhite;
}


