@-webkit-keyframes text_blink {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes text_blink {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.text.active:after {
    content: '|';
    -webkit-animation: text_blink .5s ease-in-out 0s infinite alternate;
            animation: text_blink .5s ease-in-out 0s infinite alternate;
}

.text {
    text-align: left;
    white-space: pre-wrap;
    max-width: 100%;
}


