.items-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: -webkit-linear-gradient(top, #928377 0%, #6D6460 200px, #A79A92 100%);
    background: -o-linear-gradient(top, #928377 0%, #6D6460 200px, #A79A92 100%);
    background: linear-gradient(to bottom, #928377 0%, #6D6460 200px, #A79A92 100%);
    overflow: hidden;
}

.items-wrapper.landscape {
    height: 200%;
}

.items-wrapper:not(.show) {
    display: none;
}

.items-search {
    width: 360px;
    height: 50px;
    margin: 50px auto 0;
    -webkit-box-shadow: inset 0 0 5px bisque;
            box-shadow: inset 0 0 5px bisque;
    border: solid 1px wheat;
    background: #454545;
}

.items-search>input[type="search"] {
    outline: none;
    border: none;
    width: 350px;
    height: 40px;
    background: transparent;
    color: #F7F336;
    font-size: x-large;
}

.items {
    width: 360px;
    height: calc(100% - 150px);
    margin: 0 auto;
    /* border-radius: 10px; */
    -webkit-box-shadow: inset 0 0 10px bisque;
            box-shadow: inset 0 0 10px bisque;
    border: solid 1px wheat;
    background: #454545;
    overflow: auto;
}

.item {
    cursor: default;
    width: 100%;
    height: 3em;
    line-height: 3em;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#026161), to(#21212E));
    background-image: -webkit-linear-gradient(top, #026161, #21212E);
    background-image: -o-linear-gradient(top, #026161, #21212E);
    background-image: linear-gradient(to bottom, #026161, #21212E);
    padding: 0 1em;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    white-space: nowrap;
    color: whitesmoke;
}

.item:hover,
.item:active {
    background-image: -webkit-gradient(linear, left top, left bottom, from(#026060), to(#202010));
    background-image: -webkit-linear-gradient(top, #026060, #202010);
    background-image: -o-linear-gradient(top, #026060, #202010);
    background-image: linear-gradient(to bottom, #026060, #202010);
}


